.navbar {
  z-index: 100;
  width: 100%;
  padding: 20px 0;
  transition: all .4s;
  position: fixed;
  top: 0;
}

.navbar .mobile-logo {
  z-index: 2;
  position: absolute;
  top: 43px;
  left: 80px;
}

@media (width >= 768px) {
  .navbar .mobile-logo {
    display: none;
  }
}

.navbar .mobile-nav {
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;
  display: block;
  position: relative;
  top: 25px;
  left: 25px;
}

@media (width >= 768px) {
  .navbar .mobile-nav {
    display: none;
  }
}

.navbar .mobile-nav a {
  color: #f9f9f9;
  text-decoration: none;
  transition: color .3s;
}

.navbar .mobile-nav a:hover {
  color: #f6f6f6;
}

.navbar .mobile-nav input {
  cursor: pointer;
  opacity: 0;
  z-index: 2;
  -webkit-touch-callout: none;
  width: 40px;
  height: 32px;
  display: block;
  position: absolute;
  top: -7px;
  left: -5px;
}

.navbar .mobile-nav span {
  z-index: 1;
  transform-origin: 4px 0;
  background: #f9f9f9;
  border-radius: 3px;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  transition: transform .5s cubic-bezier(.77, .2, .05, 1), background .5s cubic-bezier(.77, .2, .05, 1), opacity .55s;
  display: block;
  position: relative;
}

.navbar .mobile-nav span:first-child {
  transform-origin: 0 0;
}

.navbar .mobile-nav span:nth-last-child(2) {
  transform-origin: 0 100%;
}

.navbar .mobile-nav input:checked ~ span {
  opacity: 1;
  background: #f9f9f9;
  transform: rotate(45deg)translate(-2px, -1px);
}

.navbar .mobile-nav input:checked ~ span:nth-last-child(3) {
  opacity: 0;
  transform: rotate(0)scale(.2);
}

.navbar .mobile-nav input:checked ~ span:nth-last-child(2) {
  transform: rotate(-45deg)translate(0, -1px);
}

.navbar .mobile-nav input:checked ~ ul {
  transform: none;
}

.navbar .mobile-nav .menu {
  -webkit-font-smoothing: antialiased;
  transform-origin: 0 0;
  background: #262626;
  width: 100%;
  margin: -75px 0 0 -25px;
  padding: 125px 50px 50px;
  list-style-type: none;
  transition: transform .5s cubic-bezier(.77, .2, .05, 1);
  position: absolute;
  transform: translate(-100%);
}

.navbar .mobile-nav .menu li {
  padding: 10px 0;
  font-size: 22px;
}

.navbar .default-nav {
  visibility: hidden;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media (width >= 768px) {
  .navbar .default-nav {
    visibility: visible;
  }
}

